import { Injectable } from '@angular/core';
import { CoreException } from '@nequi/commons/exceptions';
import { BarcodeInformationEntity, BarcodeStatus } from '@nequi/features/commons/core';
import {
  InstallmentList,
  ModuleCodeEngine,
  ModuleEntity,
  OccupationEngineEntity,
  YourMoneyEngineEntity
} from '@nequi/features/credits/core';
import { ConfigModule, SourceFlow } from '@nequi/features/profile/presentation';
import { NavButtonCardStatus } from '@nequi/ui';
import { createFormat } from '@nequi/utils/string';

import { ApprovalFrontConfig } from './approval-front.config';
import { ApprovalFrontModule } from './constants/approval-front-module';

export interface EngineModuleItemList<T = unknown> {
  id: ApprovalFrontModule;
  title: string;
  description: string;
  status: NavButtonCardStatus;
  link: string;
  config?: T;
}

export interface AprovalFrontModules {
  barcodeInfo: BarcodeInformationEntity;
  occupationData: OccupationEngineEntity;
  yourMoneyData: YourMoneyEngineEntity;
  documentScan: BarcodeStatus;
  completed: number;
}

interface ApprovalAttempt {
  isCallService: boolean;
  error: CoreException;
  hasAttempts: boolean;
  maxAttempt: number;
}

interface CurrentInstallment {
  name: string;
  value: number;
  amount: number;
}

interface ApprovalFront {
  installmentList: InstallmentList[];
  attempt: ApprovalAttempt;
  isFromApproval: boolean;
  isAvailable: boolean;
  currentInstallment: CurrentInstallment;
}

@Injectable()
export class ApprovalFrontViewModel {
  public config = ApprovalFrontConfig;
  public items: EngineModuleItemList[] = [...this.config.navButtons];
  public navButtons: EngineModuleItemList[];
  public modules: AprovalFrontModules = {
    barcodeInfo: null,
    occupationData: null,
    yourMoneyData: null,
    documentScan: null,
    completed: 0,
  };

  private approvalFront: ApprovalFront = {
    installmentList: null,
    attempt: {
      isCallService: false,
      error: null,
      hasAttempts: false,
      maxAttempt: null,
    },
    isFromApproval: false,
    isAvailable: false,
    currentInstallment: {
      name: null,
      value: null,
      amount: null,
    },
  };

  public get info(): ApprovalFront {
    return this.approvalFront;
  }

  public set info(newApprovalFront: ApprovalFront) {
    this.approvalFront = newApprovalFront;
  }

  public get canContinue(): boolean {
    return !!(
      this.modules.barcodeInfo &&
      this.modules.occupationData &&
      this.modules.yourMoneyData
    );
  }

  public async loadModules(data: ModuleEntity<any>[]): Promise<void> {
    this.modules.completed = 0;

    this.modules.barcodeInfo = this.findModule<BarcodeInformationEntity>(
      data,
      ModuleCodeEngine.BARCODE_INFORMATION
    );

    this.modules.occupationData = this.findModule<OccupationEngineEntity>(
      data,
      ModuleCodeEngine.OCCUPATION
    );

    this.modules.yourMoneyData = this.findModule<YourMoneyEngineEntity>(
      data,
      ModuleCodeEngine.INCOME_EXPENSES
    );

    this.navButtons = this.items.map((item) => {
      return {
        ...item,
        ...this.getModuleTexts(item),
      };
    });

    this.navButtons = this.navButtons.sort(
      (a, b) => this.config.navOrder[a.status] - this.config.navOrder[b.status]
    );
  }

  public resetValue() {
    this.info = {
      installmentList: null,
      attempt: {
        isCallService: false,
        error: null,
        hasAttempts: false,
        maxAttempt: null,
      },
      isFromApproval: false,
      isAvailable: false,
      currentInstallment: {
        name: null,
        value: null,
        amount: null,
      },
    };
  }

  private findModule<T>(data: ModuleEntity<T>[], moduleCode: ModuleCodeEngine): T {
    return (data.find((item) => item.moduleId === moduleCode)?.value as T) || null;
  }

  private getModuleTexts(item: EngineModuleItemList) {
    switch (item.id) {
      case ApprovalFrontModule.YOUR_DOCUMENT:
        return this.getYourDocument(item.config as ConfigModule);
      case ApprovalFrontModule.WHAT_DO_YOU_DO:
        return this.getWhatDoYouDo();
      case ApprovalFrontModule.YOUR_MONEY:
        return this.getYourMoney();
    }
  }

  private getYourDocument(configModule: ConfigModule): Partial<EngineModuleItemList> {
    const isCompleted = this.isCompletedDocumentScan(configModule.sourceFlow);

    if (isCompleted && this.modules.barcodeInfo) {
      const textStructure = '{0} {1}';

      return {
        title: createFormat(textStructure, [
          this.modules.barcodeInfo.name1,
          this.modules.barcodeInfo.lastName1,
        ]),
        description: createFormat(textStructure, [
          this.modules.barcodeInfo.documentType,
          this.modules.barcodeInfo.documentNumber?.toString(),
        ]),

        status: NavButtonCardStatus.COMPLETED,
      };
    }

    return this.validateModules();
  }

  private getWhatDoYouDo(): Partial<EngineModuleItemList> {
    if (this.modules.occupationData) {
      return {
        title: this.modules.occupationData.occupation?.name,
        status: NavButtonCardStatus.COMPLETED,
      };
    }

    return this.validateModules();
  }

  private getYourMoney(): Partial<EngineModuleItemList> {
    if (this.modules.yourMoneyData) {
      return {
        status: NavButtonCardStatus.COMPLETED,
      };
    }

    return this.validateModules();
  }

  private validateModules() {
    const status = {
      status:
        this.modules.completed === 0 ? NavButtonCardStatus.ACTIVE : NavButtonCardStatus.INACTIVE,
    };

    this.modules.completed++;

    return status;
  }

  private isCompletedDocumentScan(sourceFlow: string): boolean {
    let isCaptureComplete = false;
    const hasBarcodeInfo =
      !!this.modules.barcodeInfo &&
      typeof this.modules.barcodeInfo === 'object' &&
      !!this.modules.barcodeInfo.birthPlace;

    if (this.config.flowsWithExtraction.indexOf(sourceFlow) !== -1) {
      isCaptureComplete =
        this.modules.documentScan.data?.statusAnv === this.config.documentStatus.validated;

      isCaptureComplete =
        isCaptureComplete ||
        (this.modules.documentScan.data?.statusAnv === this.config.documentStatus.requestMoreInfo &&
          hasBarcodeInfo);

      isCaptureComplete =
        isCaptureComplete ||
        (this.modules.documentScan.data?.statusAnv ===
          this.config.documentStatus.requestMoreInfoWithKofaxError &&
          hasBarcodeInfo);

      return isCaptureComplete;
    }

    return this.isSourceFlowYourAccount(sourceFlow);
  }

  private isSourceFlowYourAccount(sourceFlow: string) {
    return (
      sourceFlow === SourceFlow.YOUR_ACCOUNT &&
      (this.modules.documentScan.data?.statusAnv === this.config.documentStatus.validated ||
        this.modules.documentScan.data?.statusAnv ===
          this.config.documentStatus.requestMoreInfoWithKofaxError ||
        this.modules.documentScan.data?.statusAnv === this.config.documentStatus.noValidated)
    );
  }
}
