import {
  AfterViewInit,
  Component,
  EventEmitter,
  forwardRef,
  Input,
  OnChanges,
  Output,
  SimpleChanges
} from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { InputControlValue } from '@nequi/ui';

export const MIN_AMOUNT = 0;
export const MAX_AMOUNT = 100000;
const STEP_AMOUNT = 10000;

@Component({
  selector: 'nequi-card-money-slider',
  templateUrl: './card-money-slider.component.html',
  styleUrls: ['./card-money-slider.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => CardMoneySliderComponent),
      multi: true,
    },
  ],
})
export class CardMoneySliderComponent
  extends InputControlValue
  implements AfterViewInit, OnChanges {
  @Input() public icon: string;
  @Input() public title: string;
  @Input() public amount = MIN_AMOUNT;
  @Input() public minAmount = MIN_AMOUNT;
  @Input() public maxAmount = MAX_AMOUNT;
  @Input() public stepAmount = STEP_AMOUNT;

  @Input() public isDisabled = false;
  @Input() public hideSlider = false;
  @Input() public showDecimals = false;

  @Output()
  public amountChange = new EventEmitter<number>();

  public ngOnChanges(changes: SimpleChanges): void {
    if (changes.minAmount || changes.maxAmount) {
      this.isDisabled = false;

      this.validateAmounts();
    }
  }

  public ngAfterViewInit(): void {
    this.validateAmounts();
  }

  public async setValue(value: number) {
    super.setValue(value);

    this.amount = value;
    this.amountChange.emit(value);
  }

  private validateAmounts() {
    if (this.maxAmount === this.minAmount) {
      this.isDisabled = true;
    }

    if (this.maxAmount < this.minAmount) {
      this.minAmount = MIN_AMOUNT;
      this.maxAmount = MAX_AMOUNT;
    }

    this.amount =
      this.amount < this.minAmount || this.amount > this.maxAmount ? this.minAmount : this.amount;
  }
}
