import { Injectable } from '@angular/core';
import { CustomerCreditsEntity, LoanPortfolioEntity } from '@nequi/features/credits/core';

export interface Agreement {
  agreement?: string;
  agreementFGA?: string;
}

export interface CreditFeatures {
  approvedDate?: Date;
  installment?: number;
  loanAmount?: number;
  commissionPercentage?: number;
  monthlyFee?: number;
  term?: { name: string; value: number };
  loanDestination?: string;
  productId?: string;
  expirationDateText?: string;
  paymentAmount?: number;
  paymentDate?: Date;
  monthlyInterestRate?: number;
}

@Injectable({ providedIn: 'root' })
export class CreditInfoModel {
  private customerCredit: CustomerCreditsEntity;
  private loanPortfolio: LoanPortfolioEntity;
  private creditFeatures: CreditFeatures = null;
  private agreementInfo: Agreement = null;

  public get agreement(): Agreement {
    return this.agreementInfo;
  }

  public set agreement(updateAgreement: Agreement) {
    this.agreementInfo = updateAgreement;
  }

  public get creditFeatureInfo(): CreditFeatures {
    return this.creditFeatures;
  }

  public set creditFeatureInfo(updateCreditFeatures: CreditFeatures) {
    this.creditFeatures = updateCreditFeatures;
  }

  public get loanPortfolioInfo(): LoanPortfolioEntity {
    return this.loanPortfolio;
  }

  public set loanPortfolioInfo(newLoanPortfolio: LoanPortfolioEntity) {
    this.loanPortfolio = newLoanPortfolio;
  }

  public get customerCreditInfo(): CustomerCreditsEntity {
    return this.customerCredit;
  }

  public set customerCreditInfo(newCustomerCredit: CustomerCreditsEntity) {
    this.customerCredit = newCustomerCredit;
  }

  public cleanPreapprovedInfo() {
    this.creditFeatures = null;
    this.agreementInfo = null;
  }
}
